html {
  font-size: 15px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
p {
  margin: 0 0 15px;
}
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem;
  color: #2B2A2C;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
.container {
  width: 100%;
  max-width: 1200px;
  padding-left: 1rem;
  padding-right: 1rem;
  margin: 0 auto;
}
.header {
  padding: 1.2rem 0;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.user {
  display: flex;
}
.user__avatar {
  width: 57px;
  height: 57px;
}
.user__avatar img {
  border-radius: 50%;
}
.user__content {
  padding-left: 9px;
}
.user__name {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  color: #2B2A2C;
  padding-bottom: 3px;
}
.user__prof {
  font-size: 12px;
  opacity: 0.7;
}
.nav {
  display: flex;
  height: 100%;
  align-items: center;
}
.nav__link {
  font-size: 14px;
  color: #2B2A2C;
  text-transform: uppercase;
  font-weight: 700;
  text-decoration: none;
  margin-left: 30px;
  position: relative;
}
.nav__link:after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background-color: transparent;
  position: absolute;
  bottom: -5px;
  left: 0;
  z-index: 1;
  transition: background 0.2s linear;
}
.nav__link:hover:not(.nav__link--btn):after {
  background-color: #2B2A2C;
}
.nav__link--btn {
  padding: 10px 30px;
  border: 2px solid #2B2A2C;
  border-radius: 20px;
  transition: background 0.2s linear, color 0.2s linear, box-shadow 0.2s linear;
}
.nav__link--btn:hover {
  background-color: #2B2A2C;
  color: #fff;
  box-shadow: 2px 2px 10px rgba(43, 42, 44, 0.24);
}
.intro__inner {
  display: flex;
  background: url("/images/symbols.png") right bottom no-repeat;
  border-bottom: 1px solid rgba(106, 105, 107, 0.3);
  padding-top: 103px;
  padding-bottom: 123px;
  position: relative;
}
.intro__subtitle {
  font-size: 32px;
  font-weight: 200;
  color: #2B2A2C;
}
.intro__title {
  margin-bottom: 5px;
  font-size: 45px;
  color: #2B2A2C;
  font-weight: 700;
}
.intro__text {
  font-size: 18px;
  color: #2B2A2C;
  opacity: 0.7;
  margin-bottom: 25px;
}
.intro__content {
  width: 50%;
}
.intro__photo {
  position: absolute;
  bottom: -1px;
  right: 0;
  z-index: 1;
}
.social {
  display: flex;
  margin-bottom: 42px;
}
.social__link {
  margin-right: 10px;
  text-decoration: none;
  transition: transform 0.2s linear;
}
.social__link:hover {
  transform: translateY(-8px);
}
.btn {
  display: inline-block;
  vertical-align: top;
  font-size: 16px;
  color: #2B2A2C;
  text-decoration: none;
  text-transform: uppercase;
  font-family: inherit;
  line-height: 16px;
  text-align: center;
  background: none;
  cursor: pointer;
  padding: 20px 26px;
  border: 1px solid #6A696B;
  border-radius: 30px;
  margin-right: 10px;
  min-width: 170px;
  transition: background 0.2s linear, color 0.2s linear, box-shadow 0.2s linear;
}
.btn:focus {
  outline: none;
}
.btn:hover {
  background-color: #2B2A2C;
  color: #fff;
  box-shadow: 2px 2px 10px rgba(43, 42, 44, 0.24);
}
.btn + .btn {
  margin-left: 15px;
}
.btn-small {
  font-size: 14px;
  font-weight: 700;
  border: 2px solid #2B2A2C;
  padding: 15px 25px;
}
.works {
  padding: 41px 0;
}
.works__nav {
  display: flex;
  justify-content: center;
  margin-bottom: 35px;
}
.works__nav-link {
  font-size: 12px;
  color: #2B2A2C;
  opacity: 0.7;
  text-decoration: none;
  margin: 0 20px;
  transition: text-decoration 0.2s linear;
}
.works__nav-link:hover {
  text-decoration: underline;
}
.portfolio {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
  margin-bottom: 42px;
}
.portfolio__col {
  width: 33.3333%;
  padding: 15px;
}
.work {
  overflow: hidden;
  box-shadow: 0 0 4px rgba(43, 42, 44, 0.25);
  border-radius: 10px;
  margin-bottom: 30px;
  transition: box-shadow 0.2s linear;
}
.work:hover {
  box-shadow: 0 0 27px rgba(43, 42, 44, 0.25);
}
.work__image {
  display: block;
  max-width: 100%;
  height: auto;
}
.work__content {
  padding: 16px 12px;
}
.work__cat {
  font-size: 12px;
  color: #2B2A2C;
  text-transform: uppercase;
  margin-bottom: 8px;
}
.work__title {
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  color: #2B2A2C;
  font-weight: 700;
  text-transform: uppercase;
}
.about {
  margin: 128px 0;
}
.about__inner {
  display: flex;
  flex-wrap: wrap;
  max-width: 1090px;
}
.about__photo {
  width: 50%;
  padding-right: 45px;
  position: relative;
}
.about__photo img {
  position: relative;
  z-index: 2;
}
.about__photo:before {
  content: "";
  height: 413px;
  width: 413px;
  background-color: #EFEFEF;
  position: absolute;
  left: 15px;
  bottom: 0;
  z-index: 1;
}
.about__content {
  width: 50%;
  margin-top: 30px;
}
.about__title {
  font-size: 45px;
  color: #2B2A2C;
  font-weight: 700;
  text-transform: uppercase;
}
.about__bigtitle {
  font-size: 92px;
  line-height: 1.1;
  color: #2B2A2C;
  opacity: 15%;
  text-transform: uppercase;
  position: relative;
  left: -10px;
  margin-bottom: 20px;
}
.about__text {
  font-size: 12px;
  color: #6A696B;
  line-height: 24px;
  font-weight: 300;
  margin-bottom: 40px;
}
.reviews {
  padding: 68px 0;
  position: relative;
  background: #332F30 url("/images/reviews-bg.png") center no-repeat;
}
.reviews__item {
  width: 100%;
  max-width: 830px;
  margin: 0 auto;
}
.reviews__text {
  font-size: 14px;
  color: #fff;
  line-height: 32px;
  text-align: center;
}
.reviews__author {
  width: 100%;
  margin-top: -51px;
  text-align: center;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1;
}
.reviews__photo {
  display: block;
  margin: 0 auto 9px;
  height: 103px;
  width: 103px;
  border-radius: 50px;
  border: 4px solid #EFEFEF;
}
.reviews__name {
  margin-bottom: 4px;
  font-size: 16px;
  color: #2B2A2C;
  font-weight: 700;
}
.reviews__company {
  margin-bottom: 5px;
  font-size: 14px;
  color: #6A696B;
  font-style: italic;
  font-weight: 300;
}
.reviews__star {
  width: 13px;
  height: 13px;
}
.reviews__star--unactive {
  opacity: 0.35;
}
.text-center {
  text-align: center;
}
.img {
  display: block;
  max-width: 100%;
  height: auto;
}

